import axios from 'axios';

// ## ARTIST ##

//   
// 200:     
// 500:  
export const updateArtistProfileImage = async (formData) =>
  await axios({
    url: '/api/profile/updateArtistProfileImage',
    method: 'put',
    headers: { 'content-type': 'multipart/form-data' },
    data: formData,
  });
