export const getMyHotelProfileImage = async () =>
  await axios.get('/api/profile/myHotelProfileImage');

//   
// 200:  
// 500:  
export const updateHotelProfile = async ({
  hotelName,
  address,
  phoneNumber,
  email,
  introduceText,
}) =>
  await axios.put('/api/profile/updateHotelProfile', {
    hotelName,
    address,
    phoneNumber,
    email,
    introduceText,
  });
