import axios from 'axios';

//     
//   recruitment _id 
// :/recruitment_id
// 200:   JSON 
// 400: recruitment_id recruitment   
// 500:  
export const artistMakeApplication = async (recruitment_id) =>
  await axios.get(`/api/match/artistMakeApplication/${recruitment_id}`);

//   
// 200: 
// 500:  
export const artistSendApplication = async ({
  hotelAuth_id,
  recruitment_id,
  name,
  birthday,
  address,
  phoneNumber,
  email,
  hotelName,
  recruitmentTitle,
}) =>
  await axios.post('/api/match/artistSendApplication', {
    hotelAuth_id,
    recruitment_id,
    name,
    birthday,
    address,
    phoneNumber,
    email,
    hotelName,
    recruitmentTitle,
  });
